
:root {
    --float-duration: 3s;
}

body {
    margin: 0;
    padding: 0;
    background-color: #110f37;
}

#main.show {
    animation: horizontalOpen 0.5s ease-in-out forwards;
}

#main.close {
    animation: horizontalClose 0.5s ease-in-out forwards;
}

#carusello {
    margin-top: 5%;
}

main {
    margin-top: 100px;
    height: auto;
    width: auto;
    transform-origin: center;
}

#main {
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out;
    color: white;
    position: relative;
    overflow: hidden;
    background-color: #0d0c2b;
    border-radius: 1em;
    margin: auto;
    width: 80%;
    text-align: center;
    box-sizing: border-box;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 0.5s ease-in-out;
}

@media (max-width: 600px) {
    #main {
        width: 90%;
        margin: 10px auto;
        border-radius: 2%;
    }
}

#about {
    margin: 10px;
    box-sizing: border-box;
    padding: 20px;
    padding-left: 15%;
    padding-right: 15%;
    transition: opacity 1s ease-out, transform 1s ease-out;
    opacity: 0;
    transform: translateY(50px);
}

.download-cv-button {
    font-size: var(--font-size-xlarge);
    display: inline-block;
    background-color: #007bff;
    color: white;
    padding: 0.3em 0.6em;
    margin: 1em;
    text-align: center;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.download-cv-button:hover {
    background-color: #0056b3;
}

#about.visible {
    opacity: 1;
    transform: translateY(0);
}

#about h1 {
    font-size: var(--font-size-3xlarge);
}

@keyframes shine {
    0% {
        background-position: -40vw;
    }
    100% {
        background-position: 40vw;
    }
}

.clickable-text {
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
    border-radius: 3px;
    background-color: transparent;
    transition: background-color 0.3s, color 0.3s;
}

.clickable-text:hover {
    color: #007bff;
    text-decoration: none;
}


.profile-image {
    width: 25vw;
    height: auto;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid transparent;
    background-image: linear-gradient(#2b2850, #2b2850),
    linear-gradient(
            to right,
            rgba(17, 15, 54, 0.2),
            rgba(50, 52, 93, 0.5) 50%,
            rgba(17, 15, 44, 0.2)
    );

    background-origin: border-box;
    background-clip: content-box, border-box;
    box-shadow: 0 0 10px rgba(198, 199, 201, 0.1);
    margin-bottom: 20px;

    animation: shine 4s infinite linear;
}

#paragraph {
    font-size: var(--font-size-large);
    margin-bottom: 4%;
    padding: 10px;
    line-height: 1.6;
    background-color: #0d0c2b;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
}

.terminalinfo {
    padding: 10px;
    margin-bottom: 1rem;
    font-size: var(--font-size-medium);
}

.karaoke-text span {
    transition: color 0.3s ease;
}

/* //////////////////////////// */
#projectShow {
    display: flex;
    justify-content: space-between;
    margin: 0 9rem;
    flex-wrap: nowrap;
}

.leftShow {
    width: 100%;
    border-right: 1px solid #ccc;
    padding-right: 0.2rem;
    box-sizing: border-box;
}

.rightShow {
    width: 50%;
}

.browser-mockup,
.vscode-mockup {
    border-radius: 5px;
    overflow: hidden;
}

.browser-header {
    background: #0f1527;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.browser-buttons {
    display: flex;
    gap: 5px;
}

.browser-button {
    width: 12px;
    height: 12px;
    border-radius: 50%;
}

.browser-button.red {
    background: #ff5f56;
}

.browser-button.yellow {
    background: #ffbd2e;
}

.browser-button.green {
    background: #27c93f;
}

.browser-content {
    padding: 10px;
    height: 18.5rem;
    background: white;
    text-align: center;
}

.browser-content img {
    max-width: 200px;
    height: auto;
    margin-bottom: 1rem;
}

.browser-content h1 {
    color: black;
    font-size: var(--font-size-large);
}

.vscode-content {
    padding: 10px 0;
    height: 20rem;
    background: #1e1e1e;
}

.vscode-title {
    font-weight: bold;
    color: white;
}

.vscode-content pre {
    font-size: var(--font-size-xsmall);
    color: #d4d4d4;
    padding: 10px;
    border-radius: 5px;
    overflow-x: auto;
    display: flex;
    text-align: left;
    height: auto;
}

.vscode-content pre code {
    color: #d4d4d4;
    text-align: left;
}

@media (max-width: 1280px) {
    .vscode-content pre {
        font-size: 0.55rem;
    }
}

@media (max-width: 1180px) {
    #projectShow {
        padding: 0;
    }
}

@media (max-width: 1000px) {

    .browser-content img {
        max-width: 150px;
    }
}

@media (max-width: 550px) {
    .leftShow {
        width: 100%;
        border-right: 0;
    }

    .rightShow {
        width: 0%;
    }

    .browser-content h1 {
        color: black;
        font-size: var(--font-size-large);
    }
}

/* //////////////////////////// */

.projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin: 20px 0;
    transition: max-height 0.5s ease;
    z-index: 1;
}

.project {
    background-color: #f0f0f0;
    border-radius: 25px;
    box-shadow: 10px 8px 16px rgba(115, 107, 107, 0.1);
    overflow: hidden;
    width: calc(100% / 3 - 20px);
    max-width: 300px;
    animation: float 4s ease-in-out infinite, fadeIn 0.6s ease-out;
    cursor: pointer;
}

.project:nth-child(1) {
    animation-delay: 1s;
}

.project:nth-child(2) {
    animation-delay: 2s;
}

.project:nth-child(3) {
    animation-delay: 3s;
}

.project:nth-child(4) {
    animation-delay: 4s;
}

.project:nth-child(5) {
    animation-delay: 5s;
}

.project:nth-child(6) {
    animation-delay: 6s;
}

.project:nth-child(7) {
    animation-delay: 7s;
}

.project:nth-child(8) {
    animation-delay: 8s;
}

.project:nth-child(9) {
    animation-delay: 9s;
}

.project:nth-child(10) {
    animation-delay: 10s;
}

/* ///////////////////////////// */

.project-image {
    width: auto;
    height: 13em;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    overflow: hidden;
}

.project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
    filter: grayscale(20%);
}

.project-image img:hover {
    transform: scale(1.1);
    filter: grayscale(0);
}

.project-info {
    padding: 1.2em;
    text-align: left;
}

.project-info h2 {
    margin: 0 0 2px 0;
    color: #333;
    font-weight: bold;
}

.read-more {
    display: inline;
    cursor: pointer;
    color: #007bff;
    font-weight: bold;
}

.read-more:hover {
    color: #0056b3;
}

.project-info p {
    margin: 0;
    color: #666;
    font-size: 1.1em;
    display: inline-block;
}

.read-more::before {
    content: "\A";
    white-space: pre;
}

.link {
    font-size: 1em;
    padding: 10px 30px;
    line-height: 1.5;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: auto;
    white-space: nowrap;
}

.link:hover,
.link:focus {
    background-color: #0056b3;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    text-decoration: none;
    color: white;
}


.terminalinfo {
    padding: 10px;
    margin-bottom: 1rem;
    font-size: var(--font-size-medium);
}

.terminalinfo .terminal-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #0d0c2b;
    color: #e6e6f0;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    table-layout: fixed;
}

.terminalinfo .terminal-table th,
.terminalinfo .terminal-table td {
    text-align: center;
    padding: 12px 14px;
}

.terminalinfo .terminal-table thead th {
    background: #0f1527;
    font-weight: 600;
    font-size: calc(var(--font-size-medium) * 0.95);
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.terminalinfo .terminal-table tbody td {
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
    vertical-align: middle;
    word-wrap: break-word;
}

.terminalinfo .terminal-table tbody tr:nth-child(odd) {
    background-color: #0f1136;
}

.terminalinfo .terminal-table code {
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    background: rgba(255, 255, 255, 0.06);
    padding: 2px 6px;
    border-radius: 6px;
    display: inline-block;
}

.terminalinfo .terminal-hint {
    margin: 10px 2px 0;
    color: #cfd3ff;
    font-size: calc(var(--font-size-medium) * 0.92);
}

.clickable-text {
    color: #5aa2ff;
    cursor: pointer;
    text-decoration: underline;
    border-radius: 4px;
    background-color: transparent;
    transition: background-color 0.2s, color 0.2s, outline 0.2s;
    outline: none;
}

.clickable-text:hover {
    color: #7bb6ff;
    text-decoration: none;
}

.clickable-text:focus-visible {
    outline: 2px solid #7bb6ff;
    outline-offset: 2px;
}

@media (max-width: 600px) {
    .terminalinfo .terminal-table thead {
        display: none;
    }

    .terminalinfo .terminal-table,
    .terminalinfo .terminal-table tbody,
    .terminalinfo .terminal-table tr,
    .terminalinfo .terminal-table td {
        display: block;
        width: 100%;
    }

    .terminalinfo .terminal-table tr {
        background: #0d0c2b;
        margin-bottom: 10px;
        border: 1px solid rgba(255, 255, 255, 0.06);
        border-radius: 10px;
        overflow: hidden;
    }

    .terminalinfo .terminal-table td {
        border: 0;
        padding: 10px 12px;
    }
}


/* /////////////////////////// */

@media (max-width: 1350px) {
    #paragraph {
        padding-left: 8%;
        padding-right: 8%;
    }
}

@media (max-width: 1100px) {
    .terminalinfo {
        font-size: var(--font-size-small);
    }

    #paragraph {
        padding-left: 4%;
        padding-right: 4%;
    }

    .project {
        font-size: 1em;
        width: calc(100% / 2 - 10px);
    }
}

@media (max-width: 920px) {
    .download-cv-button {
        font-size: 0.9em;
    }

    .profile-image {
        width: 32vw;
        height: auto;
    }

    #about {
        padding-left: 10%;
        padding-right: 10%;
    }

    #about h1 {
        font-size: var(--font-size-2xlarge);
    }
}

@media (max-width: 700px) {
    .profile-image {
        width: 40vw;
        height: auto;
    }

    .project-info h2 {
        font-size: 1.38em;
    }

    .project {
        font-size: 0.7em;
        width: calc(100% / 2 - 10px);
    }

    #paragraph {
        font-size: var(--font-size-small);
        padding: 1%;
        padding-top: 6px;
    }

    .terminalinfo {
        text-align: left;
    }
}

@media (max-width: 1100px) {
    .textAnim-container {
        font-size: 4vw;
    }
}

@media (max-width: 1000px) {
    .link {
        font-size: 0.7em;
        border-radius: 10%;
        padding: 10%;
        width: auto;
        height: 4em;
    }
}

.textAnim-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    margin: 20px 0;
    font-size: 2vw;
}

@media (max-width: 800px) {
    .textAnim-container {
        margin-bottom: -13%;
    }
}

@media (max-width: 500px) {
    #about {
        padding: 0%;
    }

    .project-info {
        padding: 0.8em;
    }

    .textAnim-container {
        margin-bottom: -20%;
        font-size: 5vw;
    }

    #about h1 {
        font-size: var(--font-size-xlarge);
    }
}


@media (max-width: 400px) {

    .project {
        width: 100%;
        max-width: none;
        font-size: 0.9em;
    }
}

/* //////////////////////////// */

@keyframes blinkCursor {
    from {
        border-color: transparent;
    }
    to {
        border-color: #957290;
    }
}

@keyframes float {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes horizontalOpen {
    from {
        transform: scaleX(0);
    }

    to {
        transform: scaleX(1);
    }
}

@keyframes horizontalClose {
    from {
        transform: scaleX(1);
    }
    to {
        transform: scaleX(0);
    }
}

@keyframes fillBorderVertical {
    to {
        top: 0;
    }
}

@keyframes fillBorderHorizontal {
    to {
        left: 0;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}
